import With_window from "../../../with_window/With_window";
import C from "./Current_to_do.module.css"
import { useState } from "react";
let num = 0

const Current_to_do=props=>{


// const change_check = (e)=>{
// console.log(props.change_galochka)
// }


const [change_input, setChange_input]=useState(false)
const [value,setValue]=useState(props.value )
const [window,setWindow]=useState(null)

const change_current_input=e=>{
 
    setValue(e.target.value)
    console.log(++num);
}



const change_input_fun=()=>{
    setChange_input(true)
}
const new_span=()=>{
    if(value.trim()){
           setChange_input(false)
     
    }else{

        setWindow(<With_window window={setWindow}/>)
    }
    props.change_value(props.id, value)
    
    
    
}

    return(

       
        <div className={C.global}>

         <input 
         checked={props.packed} 
         onChange={()=>props.change_galochka(props.id)}
         type="checkbox"
         />

         {
             change_input 
             ? <input type="text" value={value} onChange={change_current_input} onBlur={new_span}/>
             : <span onDoubleClick={change_input_fun}>
            {value}
     
         </span>
         
         }
        {change_input}
         {window}
         <button onClick={ ()=> props.current_delate(props.id)}>X</button>
     

        </div>
        
      
    )
}
export default Current_to_do