const get_history=(data,his)=>{
    // console.log(data);
    if(data) {

        data.forEach(it=>{
            his.push([...it])
        })
    }
    his.push([...all_div])
    
}

///>
const localnew=()=>{
    
    props.history.setItem("globalInf", globalInf)

}



const history_push=(id)=>{
 let his = []

 const person =  all_div.map((item,index)=>{
         if(item.id===id){
            
             item.value= globalInf.current_step? "X":"O"
         }
         return item
     })


   props.history.getItem('history')
    .then(item=>{

       
//if(!item) return 


         props.history.setItem('history', [[...person]])
     get_history(item,his)
   

      props.history.setItem('history', his)
    })    
}
