///>
useEffect(()=>{
    first_loc()

    props.history.getItem('history')
    .then(item=>{
        if(!item) return  

        if(item.length){

const arr = item[item.length-1]
// console.log(arr[0].value)


const ans =   currentAr_answer

.find(i => w_win(i[0],i[1],i[2], arr))


if(ans){
    change_all_button()
  setAll_div(all_current_div)
}else{

    setAll_div(arr)
}


}


setDefoult(defoult.map((it,ind)=>{
     if(item[item.length-1][ind].value){
        return item[item.length-1][ind].value
     }
     return null
 }))


    })    
},[])



const back=()=>{ 
    change_inform(-1)
    if(defoult.filter((item=>item===null)).length === 8){
        setDefoult(win_defoul)
        props.history.setItem('history',null)
          return setAll_div(
            all_current_div.map(it=>{
                it.value = null
                  return it
              })
          )
    }


    props.history.getItem('history')
    .then(it=>{
        if(!it) return
       
        if(!it[it.length-2]) {
         
          props.history.setItem('history',null)

          setDefoult(win_defoul)

          return setAll_div(
              it[0].map(it=>{
                  it.value = null
                  return it
              })
          )
        }
        
        setAll_div(it[it.length-2])
        
    
        props.history.setItem('history',    
         it.filter((item,index,it)=>{
                if(index<it.length-1){
                    return item
                }
            })
            )
        }
    )   
}

const change_inform=(num)=>{

    let player=globalInf.current_step?"player_first":"player_second"
    
    if(num === -1){
        player  = player  === "player_first"
        ? "player_second"
        : "player_first"
    }
    
    setGlobalInf({
        
        ...globalInf,
        [player]:{
            ...globalInf[player],
            step:globalInf[player].step+num
        },
        current_step:!globalInf.current_step
        
        
    })
}



const check=()=>{


    change_all_button()

    const ans =   currentAr_answer
    .find(i => w_win(i[0],i[1],i[2]))

   if(ans){

    setAll_div(
           

        all_div.map((it,ind)=>{

    if(ans.includes(ind)){
        switch(ans){
        case currentAr_answer[0]:
        case currentAr_answer[1]:
        case currentAr_answer[2]:
           
       it.class= 'win_line win' 
        break;
        case currentAr_answer[3]:
        it.class="win_dia_left win"
        break;
        case currentAr_answer[4]:
        it.class="win_dia_right win"
        break;
        case currentAr_answer[5]:
        case currentAr_answer[6]:
        case currentAr_answer[7]:
         it.class="win_ver win"
    }
}
return it
}
    )
    )
    setWin(true)

    props.history.setItem('history',null)
    change_wins()
}

}


 const change_wins=()=>{
    const player=!globalInf.current_step?"player_first":"player_second"
    setGlobalInf({

        ...globalInf,
        [player]:{
            ...globalInf[player],
            winner:globalInf[player].winner +1
        },
        current_step:true
    })
 }






const new_game=()=>{
setDefoult(win_defoul)

setAll_div(all_div.map(item=>{
item.class="";
item.value=null
return item
}))

}


const w_win=(one,two, three, all=all_div)=>{
    
    
return all[one].value===all[two].value&&all[one].value===all[three].value&&all[one].value&&all[two].value&&all[three].value 

}

const setDiv=(id, bool=globalInf.current_step)=>{
    
   setAll_div(
       all_div.map(it=>{
           if(it.id===id){


        it.value = bool ? 'X' : 'O'
}
return it
})

)}


// const getLhistory=()=>{
 
