import { useState } from 'react'
import {generate as id} from 'shortid'
import With_window from '../../../with_window/With_window'

const Input_button=(props)=>{
const [value , setValue] = useState('')
const [window, setWindow]=useState("")
// console.log(props.change_value)

    
    
    const push_new=()=>{
        const obj = {
      value: value,
      id: id(),
      packed: false, 
      filter: false  
        }

        if( obj.value.trim()){
            
            props.change_value.setLists(
            [...props.change_value.lists,obj])
        }else{
         setWindow( <With_window window={setWindow} />)
        }
       
       
        
    }
       
        
    
    
return(
<div className="button_input">
    <input 
    //!
    // style={{color:'red'}}
    onInput={e=> setValue(e.target.value) } 
    value={value} 
    type="text"
    />
    <button onClick={push_new} >Click</button>
    <div className="input_empty">
       {window}  
    </div>
   
</div>

)
}
export default Input_button