import Current_to_do from "./Current_to_do";
import C from "./All_to_do.module.css"

const All_to_do=(props)=>{
    // console.log(props.change_galochka);
     const cur_to_do_true=props.list
     .filter(item=>item.packed)
     .map(item=><Current_to_do key={item.id} {...item} current_delate={props.current_delate} change_galochka={props.change_galochka} change_value={props.change_value} />)

     const cur_to_do_false=props.list
     .filter(item=>!item.packed)
     .map(item=><Current_to_do key={item.id} {...item} current_delate={props.current_delate} change_galochka={props.change_galochka} change_value={props.change_value} />)
    

         return(
             <div className={C.main}>  
                 
               
            <div className={C.left}>
                {cur_to_do_false}
            </div>
             <div className={C.right}>
           {cur_to_do_true}
           
             </div>



</div>

)
}
export default All_to_do



