import style from "./Win.module.css"


function Win(props){


const close=()=>{
    props.setWin(false)
    props.setNew()
}



return(
    <div className={style.global_win}>
       <div className={style.in}>
          <p>You win!</p> 
       <button onClick={close} className={style.b}>X</button> 
           </div>
    </div>
    
)

}

export default Win