import Input_button from "./conteiner_inp_but/Conteiner_inp_but"
import All_to_do from "./all_to_do/All_to_do";
import {generate as id} from 'shortid'
import { useState } from "react";




let list = [
    { value: "Pants", id: id(), packed: false, filter: false},
    { value: "Jacket", id: id(), packed: false, filter: false},
    { value: "iPhone Charger", id:id(), packed: false, filter: false},
    { value: "MacBook", id:id(), packed: false, filter: false},
    { value: "Sleeping Pills", id:id(), packed: true, filter: false},
    { value: "Underwear", id:id(), packed: false, filter: false},
    { value: "Hat", id:id(), packed: false, filter: false},
    { value: "T-Shirts", id:id(), packed: false, filter: false},
    { value: "Belt", id:id(), packed: false, filter: false},
    { value: "Passport", id:id(), packed: true, filter: false},
    { value: "Sandwich", id:id(), packed: true, filter: false}
  ];



const TO_DO =()=>{
const [lists, setLists] = useState(list)  // description


const delate=id=>setLists(lists.filter(item=>item.id!==id))

const change_galochka=id=>setLists(lists.map(item=>{
    if(item.id===id){
        item.packed=!item.packed
    }
    return item
}))
//!
const change_value=(id,value)=>setLists(lists.map(item=>{
item.id===id &&(item.value=value)
return item
}))

//!
// const window_add=()=>{
//     setWindow(<With_window />)
// }
// const window_delate=()=>{
//     setWindow(null)
// }

    return(
        <div>
<Input_button change_value={{lists, setLists}}/>
<All_to_do list={lists} change_value={change_value} current_delate={delate} change_galochka={change_galochka} />

{/* <button onClick={window_add}>+</button> 
{window}
<button onClick={window_delate}>-</button> */}
        </div>
    )
}
export default TO_DO