



function Current_div(props){
   
    const current=(e)=>{
     
        if(props.item.value) return;
const bol=e.target.parentNode.title==="true"

props.setDiv(props.item.id, bol)

 props.history(props.item.id)

}

    return(
<div onClick={current} title={props.title} className={props.item.class} >
    {props.item.value}
</div>
    )

}
export default Current_div